.draggable {
    position: absolute;
    cursor: move;
    -webkit-transition: opacity 0.3s;
    -o-transition: opacity 0.3s;
    transition: opacity 0.3s;
}

.draggable--dragging {
    opacity: 0.8;
    -webkit-transform: translateY(-5px);
        -ms-transform: translateY(-5px);
            transform: translateY(-5px);
}

.item__canvas {
    background: #F6F6F6;
    margin: 3em 3em 3em 3em;
}


